body {
  margin: 40px 40px 200px 40px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

canvas {
  border: 1px solid #b2b2b2;
}

.overlay-container {
  position: relative;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 1;
}

.overlay canvas {
  border-color: transparent;
}

h2, h3, h4 {
  text-align: center;
}

.network-layer {
  padding: 8px 0;
}

.network-kernels {
  padding: 4px 0;
}

.network-kernels * {
  display: inline !important;
}

.draw-test h3, .draw-test h4 {
  text-align: left;
}
.draw-test h3 {
  margin-bottom: 4px;
}
.draw-test h4 {
  padding: 4px;
  margin: 0;
}

.toggle-types .MuiToggleButton-root {
  color: rgba(0, 0, 0, 0.54);
}

.toggle-types .MuiToggleButton-root.Mui-selected {
  color: rgba(0, 0, 0, 0.87);;
}
